%main-button-style {
  border-radius: 5px;
  border: none;
  color: #fff;
  font-weight: 700;
}

.GoodList {
  &__button {
    background-color: #dda0dd;
    padding: 20px 15px;
    @extend %main-button-style;
    transition: 1s;
    font-size: 16px;
    margin-top: 20px;
    margin-bottom: 20px;

    &:not(:last-child) {
      margin-right: 5px;
    }

    &:hover {
      transition: 1s;
      box-shadow: 0 0 50px #b0c4de;
    }

    &--reset {
      background-color: #ff6347;
    }
  }

  &__goods {
    display: inline-block;
    position: absolute;
    right: 50%;
    bottom: 0;
    transform: translateX(50%);
    border-radius: 5px;
    box-shadow: 0 0 1em rgba(white, 0.2), inset 0 0 1px rgba(white, 0.8);
    background-color: lighten(#1fbded, 20%);
  }

  &__goods-select {
    width: auto;
    margin: 0;
    padding: 0.75em 1.5em;
    outline: none;
    cursor: pointer;
    border: none;
    border-radius: 0;
    color: #fff;
    background-color: transparent;
    font-family: "Noto Serif", serif;
    font-size: 16px;
    font-weight: 700;

    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 0.01px;
    text-overflow: "";

    &::-ms-expand {
      display: none;
    }

    &:after {
      position: absolute;
      top: 1.25em;
      right: 1em;
      width: 0;
      height: 0;
      padding: 0;
      content: "";
      border-left: 0.25em solid transparent;
      border-right: 0.25em solid transparent;
      border-top: 0.38em solid darken(#1fbded, 25%);
      pointer-events: none;
    }
  }

  &__option {
    font-family: "Noto Serif", serif;
    font-size: 16px;
    color: #192124;
  }

  &__goods-list {
    list-style: none;
    font-size: 18px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

@-moz-document url-prefix() {
  .GoodList__goods-select select {
    padding-right: 1.75em;
  }
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  .GoodList__goods-select select {
    padding-right: 2em;
  }
}
