/* put styles here */
h1,
h2,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

body {
  box-sizing: border-box;
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100vh;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

html {
  font-family: "Noto Serif", serif;
}

%main-button-style {
  border-radius: 5px;
  border: none;
  color: #fff;
  font-weight: 700;
}

.App {
  padding: 20px;
  text-align: center;

  &__title {
    font-size: 40px;
    text-align: center;
    font-weight: 700;
    margin-bottom: 30px;
  }

  &__wrapper {
    margin: auto;
    padding: 20px 10px 60px;
    width: 500px;
    text-align: center;
    position: relative;
  }

  &__start-button {
    padding: 20px 40px;
    background-color: #87cefa;
    @extend %main-button-style;
    font-size: 20px;
    transition: 0.5s;

    &:hover {
      transition: 0.5s;
      box-shadow: 0 0 100px #b0c4de;
    }
  }
}
